/* generic */
.container {
  margin: 0 auto;
  width: 100%;
  position: relative;
  padding: 0 20px;
  max-width: 1680px;
  @media (min-width: 1440px) {
    // padding: 0 80px 0 150px;
  }
}

.flex {
  display: flex;
}

.ssa_booking_iframe {
  min-height: 250px;
}

.videowrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  height: 0;
}
.videowrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.btn_group {
  display: flex;

  .btn {
    @include pop(500, 16, 16px);
    letter-spacing: 0.01em;
    text-decoration: none;
    padding: 24px 34px;
    border-radius: 6px;
  }

  .background_green {
    background: $green;
    box-shadow: 0px 6px 20px rgba(39, 168, 125, 0.15),
      0px 4px 8px rgba(39, 168, 125, 0.2025),
      0px 1px 3px rgba(39, 168, 125, 0.4);
    transition: 0.4s;

    &:hover {
      background: #24a87d;
    }
  }

  .text_white {
    color: #fff;
  }

  .text_grey {
    color: #5a6272;
  }

  .border_grey {
    border: 1.2px solid #5a6272;
  }
}

.hide_mob {
  visibility: hidden;
  width: 0;
  height: 0;
  overflow: hidden;
  padding: 0 !important;

  @media (min-width: 1440px) {
    visibility: visible;
    overflow: visible;
    width: inherit;
    height: inherit;
  }

}
