/* base */

html {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

#smooth-wrapper {
  overflow: hidden;
}

#wpadminbar {
  display: none;
}

.logged-in {
  #wpadminbar {
    display: block;
  }
}

*,
*:before,
*:after {
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

body {
  color: #333;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  margin: 0;

  &.video_active {
    overflow: hidden;
  }
}

img {
  //border: 0;
  //width: 100%;
  //height: auto;
  //display: block;
}

svg {
  width: 100%;
  height: auto;
}

button {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  background: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
form,
input,
ul,
ol,
li,
header,
nav,
footer,
section {
  list-style: none;
  margin: 0;
  padding: 0;
}

header,
nav,
footer,
section {
  display: block;
}

textarea,
button,
input,
input[type="text"],
input[type="button"],
input[type="submit"] {
  -webkit-appearance: none;
  border-radius: 0;
  color: #333;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 300;
  outline: none;
}

textarea {
  resize: none;
  overflow: auto;
}

input:focus,
textarea:focus {
  outline: none;
}

table,
table td {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0;
  padding: 0;
}

a {
  color: #333;
}
