.header {
  padding: 20px 0;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  transition: 0.4s;

  .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .left-header {
      display: flex;
      gap: 24px;

      .site-logo {
        width: 40px;
        max-width: 40px;
        display: flex;
        align-items: center;
        height: 40px;
        position: relative;
        .logo_dark {
          img {
            transition: .3s;
          }
          &:hover {
            img {
              filter: brightness(0) saturate(100%) invert(25%) sepia(60%) saturate(2256%) hue-rotate(244deg) brightness(90%) contrast(100%);
            }
          }
        }

        a {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          transition: ease all 0.3s;

          &.logo_light {
            opacity: 0;
            visibility: hidden;
          }
        }
      }

      .menu-items {
        display: none;

        @media (min-width: 1310px) {
          display: block;
        }

        ul {
          display: flex;
          flex-direction: row;
          gap: 24px;

          li {
            position: relative;

            a {
              font-family: "Figtree", sans-serif;
              line-height: 48px;
              font-style: normal;
              font-weight: 500;
              font-size: 17px;
              color: $black;
              text-decoration: none;
              transition: 0.4s;

              &:hover {
                color: #7a50ce;
              }
            }

            .sub-menu {
              display: none;
            }
          }

          .menu-item-has-children:not(.acro-megamenu) {
            &:hover {
              .sub-menu {
                position: absolute;
                display: block;
                padding: 20px 48px 20px 24px;
                background: #fff;
                box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
                  0px 24px 60px rgba(6, 47, 125, 0.05),
                  0px 12px 24px rgba(27, 59, 119, 0.05);
                border-radius: 16px;
                white-space: nowrap;

                a {
                  color: #7a50ce;
                }
              }
            }
          }
        }

        .current-menu-item {
          a {
            color: #7a50ce;
          }
          &:after {
            filter: brightness(0) saturate(100%) invert(25%) sepia(60%) saturate(2256%) hue-rotate(244deg) brightness(90%) contrast(100%);
          }
        }


        .menu-item-has-children {
          padding-right: 24px;

          &:after {
            position: absolute;
            content: "";
            width: 12px;
            height: 12px;
            top: 19px;
            right: 4px;
            background-image: url(/wp-content/themes/acro/images/svg/nav-arrow.svg);
            background-size: cover;
            transition: 0.4s;
          }
          &:hover {
            &:after {
              transform: rotate(180deg);
              filter: brightness(0) saturate(100%) invert(25%) sepia(60%) saturate(2256%) hue-rotate(244deg) brightness(90%) contrast(100%);
            }
          }
        }
      }
      .menu.hovered > li > a {
        color: #24272e;
      }
      .menu.hovered > li:after {
        filter: none !important;
      }
      .menu.hovered > li:hover  {
        &:after {
          filter: brightness(0) saturate(100%) invert(25%) sepia(60%) saturate(2256%) hue-rotate(244deg) brightness(90%) contrast(100%) !important;
        }
        & > a {
          color: $purple !important;
        }
      }
    }


    .right-header {
      display: none;

      @media (min-width: 1310px) {
        display: block;
      }

      .secondary-menu {
        display: flex;
        flex-direction: row;
        gap: 26.5px;
        margin-bottom: 0 !important;

        .secondary-menu-item {

          img {
            transition: .3s;
          }

          a {
            font-family: "Figtree", sans-serif;
            line-height: 48px;
            font-style: normal;
            font-weight: 500;
            font-size: 17px;
            color: $black;
            text-decoration: none;
            transition: 0.4s;
            display: flex;
            flex-direction: row;
            gap: 6.5px;
            align-items: center;

            &.current-menu-item, &:hover {
              color: #7a50ce;
              img {
                filter: brightness(0) saturate(100%) invert(25%) sepia(60%) saturate(2256%) hue-rotate(244deg) brightness(90%) contrast(100%);
              }
            }
          }
          &.menu-item-status {
            img {
              filter: none !important;
            }
            a {
              .menu-item-icon {
                position: relative;
                &:before {
                  content: '';
                  display: block;
                  width: 16px;
                  height: 16px;
                  position: absolute;
                  left: 2px;
                  top: 2px;
                  opacity: 0;
                  z-index: 2;
                  transition: 0.4s;
                  background: url('/wp-content/themes/acro/images/svg/status-circles.svg') no-repeat center center;
                  background-size: contain;
                }
              }
              &.current-menu-item, &:hover {
                .menu-item-icon {
                  img {
                    opacity: 0;
                  }
                  &:before {
                    opacity: 1;
                  }
                }
              }
            }
          }

          .menu-item-icon {
            position: relative;
            height: 20px;
            width: 20px;
          }

          .secondary-menu-icon {
            max-width: 20px;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transition: ease all 0.3s;

            &.menu-icon-light {
              opacity: 0;
              visibility: hidden;
            }
          }
        }
      }
    }
  }

  .mobile-menu {
    display: flex;
    flex-direction: row;
    gap: 19px;
    align-items: center;
    justify-content: center;

    @media (min-width: 1310px) {
      display: none;
    }

    .menu-cta {
      width: 128px;
    }
  }

  // Nav Scroll Overrides.

  &.nav-color-white {
    .left-header .menu-items ul li a {
      color: #fff;
    }
    .left-header .menu-items .current-menu-item a {
      color: #fff;
    }
    .left-header .menu-items .menu-item-has-children:after {
      background-image: url("../../images/nav-arrow-white.png");
    }

    .left-header .site-logo a {
      &.logo_light {
        opacity: 1;
        visibility: visible;
      }
      &.logo_dark {
        opacity: 0;
        visibility: hidden;
      }
    }

    .right-header .secondary-menu .secondary-menu-item a {
      color: #fff;
    }

    .right-header .secondary-menu .secondary-menu-item .secondary-menu-icon {
      &.menu-icon-light {
        opacity: 1;
        visibility: visible;
      }

      &.menu-icon-dark {
        opacity: 0;
        visibility: hidden;
      }
    }

    .menu-burger-dark {
      display: block !important;
    }

    .menu-burger-light {
      display: none;
    }

  }

  &.button-is-border {
    .menu-cta {
      background: none;
      color: $green !important;
      box-shadow: none;
      border: 1px solid $green;
    }
  }

  .burger {
    max-width: 22px;

    .menu-burger-dark {
      display: none;
    }

  }

}

.menu-cta {
  width: 208px;
  height: 48px;
  background: #27b989;
  box-shadow: 0px 6px 20px rgba(39, 168, 125, 0.15),
    0px 4px 8px rgba(39, 168, 125, 0.2025), 0px 1px 3px rgba(39, 168, 125, 0.4);
  border-radius: 6px;
  font-weight: 500;
  font-size: 15px;
  line-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.01em;
  color: #ffffff !important;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  transition: 0.4s;

  &:hover {
    background: #24a87d;
  }
}

.mobile-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100dvh;
  background: rgba(255, 255, 255, 0.98);
  //backdrop-filter: blur(30px);
  z-index: 999999;
  padding: 20px;
  transform-style: preserve-3d;
  transition: transform 0.5s;
  transform-origin: right center;
  transform: rotateY(-180deg);
  overflow: hiden;


  .upper-menu {
    height: 140px;

    .logo {
      max-width: 40px;
      position: absolute;
      top: 20px;
      left: 20px;
    }

    .close {
      max-width: 16px;
      position: absolute;
      top: 28px;
      right: 24px;

      img {
        width: 100%;
        height: auto;
      }

    }

  }

  .mobile-header-holder {
    min-height: calc(100vh - 180px);
    display: flex;
    flex-direction: column;
    //justify-content: space-between;
    
    .menu-items {
      margin-bottom: 24px;
      ul {
        display: flex;
        flex-direction: column;

        li {
          a {
            font-family: "Figtree", sans-serif;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 111%;
            letter-spacing: -0.02em;
            color: #1E1C21;
            text-decoration: none;
            @media (min-width: $screen-sm) {
              font-size: 24px;
            }
            @media (min-width: 1310px) {
              font-size: 17px;
            }
          }
        }

        .menu-item-has-children {
          position: relative;
          &.active {
            & > a {
              color: #7a50ce;
            }
            &:after {
              transform: rotate(180deg);
            }
          }

          &:after {
            position: absolute;
            content: "";
            display: inline-block;
            width: 12px;
            height: 12px;
            top: 21px;
            right: 20px;
            background-image: url(/wp-content/themes/acro/images/svg/nav-arrow.svg);
            background-size: cover;
            transition: 0.4s;
          }
        }

        .sub-menu {
          margin-top: 16px;
          gap: 16px;
          display: none;

          li {
            margin-bottom: 16px;

            a {
              text-decoration: underline;
              @media (min-width: 1310px) {
                text-decoration: none;
              }
              //font-size: 15px;
            }
          }
        }
      }
      .menu {
        & > li {
          border-bottom: 1px;
          border-bottom: 1px solid #D0CDD5;
          padding: 18px 22px;
          & > a {
            @media (min-width: $screen-sm) {
              font-size: 29px;
            }
            @media (min-width: 1310px) {
              font-size: 17px;
            }
          }
          &:last-child {
            border-bottom: none;
          }
        }
      }
    }



    .secondary-menu {
      width: 100%;
      height: 64px;
      border: 1px solid #babfc9;
      border-radius: 12px;
      padding: 12px 0;
      display: flex;
      flex-direction: row;
      margin-top: auto;

      .secondary-menu-item {
        display: flex;
        flex: 1;
        gap: 6.5px;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .secondary-menu-icon {
          max-width: 20px;
        }

        a {
          font-family: "Figtree", sans-serif;
          font-weight: 500;
          font-size: 15px;
          color: #4f4b58;
          text-decoration: none;
        }
      }
    }
  }

  .menu-cta {
    width: 100%;
    padding: 24px 20px;
    line-height: 1;
    height: auto;
    font-size: 16px;
    margin-top: 24px;
  }
}

.open-mobile-header {
  transform: none;
}

.scrolled:not(.hide-menu) {
  background: #FFFFFF;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12);
  transition: 0.4s;
}

.scrolled-true {
  background: #FFFFFF;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12);
  transition: 0.4s;
}

.logged-in {
  .header {
    top:32px !important;
  }

}