.solutions-container {
  width: 100%;
  padding: 165px 20px 60px 20px;
  overflow: hidden;
  background: #fff;

  @media (min-width: $screen-md) {
    height: 120vh;
    padding: 0 20px 73px;
    margin-top: 0;
    // background:linear-gradient(180deg, rgba(255, 255, 255, 1) 43%, rgba(238, 239, 242, 1) 100%);
    overflow: visible;
  }

  .title-section {
    display: flex;
    height: 75%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: 1.5s;
    max-width: 92%;
    margin: 0 auto 47.5px auto;

    @media (min-width: $screen-md) {
      max-width: 100%;
      margin: 0;
    }

    h1 {
      @include pop(400, 44, 112%, normal);
      text-align: center;
      letter-spacing: -0.02em;
      background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-bottom: 16px;
      transition: 1.5s;

      @media (min-width: $screen-md) {
        @include pop(400, 65, 100%, normal);
      }
    }

    h2 {
      @include fig(500, 23, 124%, normal);
      text-align: center;
      background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      transition: 1.5s;
    }
    .trustpilot-text {
      padding-top: 7vh;
      width: 100%;
      @include fig(500, 20, 124%);
      background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      p {
        @include fig(500, 20, 124%);
        background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        @media (min-width: $screen-md) {
          flex-direction: row;
        }
      }
      img {
        margin-left: 8px;
        vertical-align: middle;
      }
    }
  }

  .solutions-holder {
    display: none;
    transition: 1.5s;

    @media (min-width: $screen-md) {
      display: flex;
      flex-direction: row;
    }

    .solution {
      flex-basis: 25%;
      padding-left: 30px;
      position: relative;

      .left-bar {
        position: absolute;
        left: 0;
        top: 35px;
        width: 1px;
        height: 100%;
        background: linear-gradient(180deg, rgba(186, 191, 201, 1) 60%, rgba(255, 255, 255, 1) 100%);

        &:before {
          content: "";
          position: absolute;
          width: 7.5px;
          height: 7.5px;
          border-radius: 100%;
          left: -3.5px;
          top: -4px;
          background: #6436bf;
        }
      }

      .solution-icon {
        max-width: 72px;
        margin-bottom: 32px;
      }

      .sub-title {
        @include pop(500, 13, 124%, normal);
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #777184;
        margin-bottom: 14px;
      }

      h3 {
        @include pop(400, 29, 124%, normal);
        letter-spacing: -0.02em;
        background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        margin-bottom: 32px;
      }

      p {
        @include fig(400, 18, 136%, normal);
        color: #4f4b58;
        max-width: 260px;
        margin-bottom: 50px;
      }

      a {
        @include pop(400, 15, 124%, normal);
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #6436bf;
        text-decoration: none;
      }
    }

    .soft_grad {
      position: absolute;
      top: -100%;
      left: -50%;
      width: 200%;
      opacity: 2;
      z-index: -1;
    }
  }

  // &:before {
  //   position: absolute;
  //   content: "";
  //   width: 100%;
  //   height: 47vh;
  //   top: 0;
  //   left: 0;
  //   // background-image: url(../../images/banner3.png);
  //   background-size: cover;
  //   transition: 0.4s;
  // }
}

.solutions-container-active {
  &:before {
    // background-image: url(../../images/banner4.png);
  }

  .title-section {
    height: 55%;
    @media (min-width: $screen-md) {
      padding: 25vh 0 9vh;
    }

    h1 {
      font-size: 44px;

      @media (min-width: $screen-md) {
        font-size: 65px;
      }

    }

    h2 {
      font-size: 18px;

      @media (min-width: $screen-md) {
        font-size: 23px;
      }

    }

  }

  .solutions-holder {
    height: 55%;
  }
}

.mobile-solutions {
  display: block !important;
  opacity: 1;

  @media (min-width: $screen-md) {
    display: none;
    opacity: 0;
  }

  .mobile-solution-slider {
    .solution {
      background: #ffffff;
      padding: 32px;
      box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
        0px 24px 60px rgba(6, 47, 125, 0.05),
        0px 12px 24px rgba(27, 59, 119, 0.05);
      position: inherit;

      .left-bar {
        display: none;
      }
    }
  }
}

.tp-widget-review {
  background: red;
}
