.power-stats {
	position: relative;
	background: #eeeff2;
	text-align: center;
	padding: 70px 20px 0;
	img {
		max-width: 100%;
		height: auto;
		vertical-align: top;
	}
	@media (min-width: $screen-md) {
		padding: 70px 30px 40px;
	}
	&__title {
		@include titleGrad();
		@include pop(400, 44, 1.12);
		letter-spacing: -0.02em;
		margin-bottom: 34px;
	}
	&__iconboxes {
		display: flex;
		gap: 24px;
		justify-content: center;
		flex-wrap: wrap;
		margin-bottom: 24px;
		.iconbox {
			padding: 10px 14px 10px 48px;
			border-radius: 12px;
			background: #fff url('/wp-content/themes/acro/images/svg/verified.svg') no-repeat 14px center;
			background-size: 24px;
			@include pop(400, 20, 1.36);
			letter-spacing: -0.02em;
			color: #4F4B58;
			@media (min-width: $screen-sm) {
				font-size: 23px;
				background-position: 20px 16px;
				padding: 14px 20px 14px 54px;	
			}
		}
	}
	&__stats {
		display: flex;
		flex-wrap: wrap;
		gap: 24px;
		flex-direction: column;
		margin-bottom: 44px;
		width: 100%;
		max-width: 1300px;
		margin-left: auto;
		margin-right: auto;
		@media (min-width: $screen-md) {
			flex-direction: row;
		}
		.stat {
			border-radius: 12px;
			background: #fff;
			padding: 24px;
			position: relative;
			color: #000;
			overflow: hidden;
			text-align: left;
			height: 257px;
			box-shadow: 0px 6px 20px 0px rgba(104, 104, 104, 0.08), 0px 4px 8px 0px rgba(104, 104, 104, 0.12), 0px 1px 3px 0px rgba(104, 104, 104, 0.12);
			@media (min-width: $screen-md) {
				flex: 1;
			}
			.before-title {
				@include pop(400, 19, 1.24);
				margin-bottom: 9px;
			}
			.title {
				@include fig(400, 80, 0.9);
				margin-bottom: 9px;
			}
			.after-title {
				@include fig(400, 15, 1.2);
				margin-bottom: 9px;
				max-width: 50%;
			}
			.green-label {
				@include fig(500, 12, 1.33);
				border-radius: 4px;
				padding: 2px 4px;
				background: $green;
				color: #fff;
				display: inline-block;
				vertical-align: middle;
			}
			.img {
				position: absolute;
			}
			&.style-1 {
				.img {
					&1 {
						right: 24%;
						top: 44%;
						opacity: 0.5;
						filter: blur(0.5px);
						animation: acroFadePulseDown 8s linear infinite;
					}
					&2 {
						right: 11%;
						top: 53%;
						animation: acroFadePulseLeft 8s linear infinite;
						animation-delay: 2s;
					}
					&3 {
						right: 4%;
						top: 22%;
						opacity: 0.5;
						filter: blur(0.5px);
						animation: acroFadePulseUp 8s linear infinite;
					}
					&4 {
						right: 0;
						bottom: 0;
						animation: acroPulseUp 8s linear infinite;
					}
					&5 {
						animation: acroFadePulse 8s linear infinite;
						right: 10%;
						top: 17%;
						display: flex;
						align-items: center;
						justify-content: center;
						width: 67px;
						height: 67px;
						border-radius: 50%;
						box-shadow: 0px 28.5px 104.5px 0px rgba(162, 165, 168, 0.20), 0px 3.569px 13.085px 0px rgba(162, 165, 168, 0.41);
						z-index: 2;
						background: #fff;
						@media (min-width: $screen-md) {
							width: 76px;
							height: 76px;
						}
						img {
							position: relative;
							right: -2px;
							width: 36px;
							@media (min-width: $screen-md) {
								width: 42px;
							}
						}
					}
					&6  {
						top: 46%;
						right: 8%;
						z-index: 2;
						animation: acroFadePulseUpReverse 8s linear infinite;
						@media (min-width: $screen-md) {
							top: 39%;
							right: 5%;
						}
					}
				}
				&:before {
					content: '';
					display: block;
					width: 311.569px;
					height: 311.569px;
					position: absolute;
					right: 7.408px;
					bottom: -172.592px;
					transform: rotate(-75deg);
					border-radius: 311.569px;
					opacity: 0.1;
					background: var(--Gradients-01, linear-gradient(180deg, #7048C7 0%, #BD48C7 100%));
					filter: blur(59.831233978271484px);
				}
			}
			&.style-2 {
				.img {
					&1 {
						right: -2%;
						bottom: 0;
						width: 100px;
						z-index: 2;
						box-shadow: 0px 591.158px 68.188px 0px rgba(83, 45, 159, 0.07), 49.263px -32.211px 28.487px 0px rgba(83, 45, 159, 0.05), 28.421px -6.632px 15.347px 0px rgba(83, 45, 159, 0.04), 0px 0.947px 8.538px 0px rgba(83, 45, 159, 0.04), 0px 4.863px 4.535px 0px rgba(83, 45, 159, 0.03), 0px -2.359px 1.887px 0px rgba(83, 45, 159, 0.02);
						border-radius: 10px 10px 0 0;
						overflow: hidden;
						animation: acroStat2Img1 8s linear infinite;
						@media (min-width: $screen-sm) {
							width: 133px;
							right: 4.5%;
						}
					}
					&2 {
						border-radius: 6px 6px 0 0;
						right: 18%;
						bottom: -20%;
						width: 110px;
						transform: scale(0.7);
						filter: blur(0.7580298781394958px);
						opacity: 0.3;
						box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25), 0px 474.965px 54.786px 0px rgba(83, 45, 159, 0.07), 39.58px -25.879px 22.888px 0px rgba(83, 45, 159, 0.05), 22.835px -5.328px 12.331px 0px rgba(83, 45, 159, 0.04), 0px 0.761px 6.86px 0px rgba(83, 45, 159, 0.04), 0px 3.908px 3.643px 0px rgba(83, 45, 159, 0.03);
						animation: acroStat2Img2 8s linear infinite;
						@media (min-width: $screen-sm) {
							width: 142px;
						}
					}
					&3 {
						width: 48px;
						height: 48px;
						border-radius: 50%;
						background: $green;
						display: flex;
						align-items: center;
						justify-content: center;
						box-shadow: 0px 18px 66px 0px rgba(162, 165, 168, 0.20), 0px 2.254px 8.264px 0px rgba(162, 165, 168, 0.41);
						right: 34%;
						bottom: 16%;
						z-index: 4;
						animation: acroFadePulseMore 8s linear infinite;
						@media (min-width: $screen-sm) {
							right: 44%;
						}
					}
				}
				&:before {
					content: '';
					display: block;
					width: 433px;
					height: 433px;
					position: absolute;
					right: -64.489px;
					bottom: -367.489px;
					border-radius: 433px;
					opacity: 0.3;
					transform: rotate(60deg);
					background: var(--Gradients-3, linear-gradient(180deg, #B1F390 0%, #48D5C1 100.25%));
					filter: blur(83.1500015258789px);
				}
			}
			&.style-3 {
				.img {
					&1 {
						width: 26px;
						height: 26px;
						border-radius: 50%;
						background: $green;
						display: flex;
						align-items: center;
						justify-content: center;
						box-shadow: 0px 1.607px 3.214px 0px rgba(0, 0, 0, 0.10), 0px 2.143px 4.286px 0px rgba(0, 0, 0, 0.08), 0px 0.536px 6.429px 0px rgba(0, 0, 0, 0.04);
						right: 5%;
						bottom: 38%;
						animation: acroStat3Img1 8s linear infinite;
						z-index: 2;
						img {
							width: 12px;
						}
					}
					&2 {
						width: 25px;
						height: 25px;
						border-radius: 50%;
						box-shadow: 0px 11.125px 40.792px 0px rgba(162, 165, 168, 0.20), 0px 1.393px 5.108px 0px rgba(162, 165, 168, 0.41);
						filter: blur(0.5px);
						opacity: 0.6;
						display: flex;
						align-items: center;
						justify-content: center;
						right: 24%;
						bottom: 30%;
						animation: acroStat3Img2 8s linear infinite;
						img {
							width: 14px;
							@media (min-width: $screen-sm) {
								width: 18px;
							}
						}
						@media (min-width: $screen-sm) {
							width: 30px;
							height: 30px;
							right: 28%;
							bottom: 33%;
						}
					}
					&3 {
						top: 16%;
						right: 8%;
						animation: acroStat3Img3 8s linear infinite;
					}
					&4 {
						width: 32px;
						height: 32px;
						display: flex;
						align-items: center;
						justify-content: center;
						border-radius: 12px;
						background: #7048C7;
						box-shadow: 0px 9px 33px 0px rgba(112, 72, 199, 0.20), 0px 1.127px 4.132px 0px rgba(112, 72, 199, 0.40);
						right: 20%;
						top: 10%;
						animation: acroStat3Img4 8s linear infinite;
						@media (min-width: $screen-sm) {
							width: 42px;
							height: 42px;
							top: 17%;
							right: 26%;
						}
						img {
							width: 26px;
							@media (min-width: $screen-sm) {
								width: 32px;
							}
						}

					}
					&5 {
						border-radius: 5.033px;
						box-shadow: 0px 3.451px 11.217px 0px rgba(0, 0, 0, 0.10), 0px 1.4px 2.8px 0px rgba(0, 0, 0, 0.04);
						filter: blur(1.2000000476837158px);
						right: 6%;
						bottom: 7%;
						width: 150px;
						max-width: 150px;
						@media (min-width: $screen-sm) {
							max-width: 200px;
						}
						img {
							width: 100%;
						}
						animation: acroStat3Img5 8s linear infinite;
					}
					&6 {
						width: 26px;
						height: 26px;
						display: flex;
						align-items: center;
						justify-content: center;
						border-radius: 50%;
						background: #fff;
						box-shadow: 0px 12px 44px 0px rgba(162, 165, 168, 0.20), 0px 1.503px 5.509px 0px rgba(162, 165, 168, 0.41);
						filter: blur(0.2626592516899109px);
						right: 30%;
						top: 46%;
						animation: acroStat3Img2 8s linear infinite;
						img {
							width: 20px;
							@media (min-width: $screen-sm) {
								width: 22px;
							}
						}
						@media (min-width: $screen-sm) {
							width: 32px;
							height: 32px;
							right: 35%;
							top: 41%;
						}
					}
					&7 {
						width: 66px;
						height: 66px;
						display: flex;
						align-items: center;
						justify-content: center;
						box-shadow: 0px 4px 8px 3px rgba(0, 0, 0, 0.15), 0px 1px 3px 0px rgba(0, 0, 0, 0.30);
						background: #D5F6EB;
						border-radius: 25px;
						right: 10%;
						top: 28%;
						animation: acroStat3Img7 8s linear infinite;
						@media (min-width: $screen-sm) {
							width: 80px;
							height: 80px;
						}
					}
					&8 {
						border-radius: 6.046px;
						box-shadow: 0px 82px 172px 0px rgba(24, 3, 69, 0.04), 0px 34.258px 71.857px 0px rgba(24, 3, 69, 0.05), 0px 18.316px 38.418px 0px rgba(24, 3, 69, 0.06), 0px 10.268px 21.537px 0px rgba(24, 3, 69, 0.08), 0px 5.453px 11.438px 0px rgba(24, 3, 69, 0.09), 0px 2.269px 4.76px 0px rgba(24, 3, 69, 0.13);
						left: 18%;
						bottom: 14%;
						width: 193px;
						animation: acroStat3Img8 8s linear infinite;
						max-width: 143px;
						@media (min-width: $screen-sm) {
							max-width: 193px;
						}
						img {
							width: 100%;
						}
					}
				}
			}
		}
	}
	&__cta {
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		gap: 24px;
		justify-content: center;
		align-items: center;
		@media (min-width: $screen-sm) {
			flex-direction: row;
			gap: 47px;
		}
		.cta-text {
			@include pop(400, 23, 1.36);
			letter-spacing: -0.02em;
			@include titleGrad();
		}
		.cta-button {
			@include greenButton();
			width: 200px;
		}
	}
	.stars {
		width: 82px;
	}
}

@keyframes acroFadePulseUpReverse {
	0% {
		opacity: 1;
		filter: blur(0);
		transform: translateY(0);
	}
	20% {
		opacity: 0.4;
		filter: blur(0.5px);
		transform: translateY(-20px);
	}
	70% {
		opacity: 0.4;
		filter: blur(0.5px);
		transform: translateY(-20px);
	}
	90% {
		opacity: 1;
		filter: blur(0);
		transform: translateY(0);
	}
	100% {
		opacity: 1;
		filter: blur(0);
		transform: translateY(0);
	}
}

@keyframes acroFadePulseUp {
	0% {
		opacity: 0.5;
		filter: blur(0.5px);
		transform: translateY(0);
	}
	20% {
		opacity: 1;
		filter: blur(0);
		transform: translateY(-20px);
	}
	70% {
		opacity: 1;
		filter: blur(0);
		transform: translateY(-20px);
	}
	90% {
		opacity: 0.5;
		filter: blur(0.5px);
		transform: translateY(0);
	}
	100% {
		opacity: 0.5;
		filter: blur(0.5px);
		transform: translateY(0);
	}
}
@keyframes acroFadePulseDown {
	0% {
		opacity: 0.5;
		filter: blur(0.5px);
		transform: translateY(0);
	}
	20% {
		opacity: 1;
		filter: blur(0);
		transform: translateY(20px);
	}
	70% {
		opacity: 1;
		filter: blur(0);
		transform: translateY(20px);
	}
	90% {
		opacity: 0.5;
		filter: blur(0.5px);
		transform: translateY(0);
	}
	100% {
		opacity: 0.5;
		filter: blur(0.5px);
		transform: translateY(0);
	}
}

@keyframes acroFadePulseLeft {
	0% {
		opacity: 1;
		filter: blur(0);
		transform: translateX(0);
	}
	20% {
		opacity: 0.5;
		filter: blur(0.5px);
		transform: translateX(-10px) scale(0.6);
	}

	70% {
		opacity: 0.5;
		filter: blur(0.5px);
		transform: translateX(-10px) scale(0.6);
	}
	90% {
		opacity: 1;
		filter: blur(0);
		transform: translateX(0);
	}
	100% {
		opacity: 1;
		filter: blur(0);
		transform: translateX(0);
	}
}

@keyframes acroFadePulse {
	0% {
		transform: scale(1);
		z-index: 1;
	}
	20% {
		transform: scale(1.1);
		z-index: 3;
	}

	70% {
		transform: scale(1.1);
		z-index: 3;
	}
	90% {
		transform: scale(1);
		z-index: 1;
	}
	100% {
		transform: scale(1);
		z-index: 1;
	}
}


@keyframes acroFadePulseMore {
	0% {
		transform: scale(1);
		z-index: 1;
		box-shadow: 0px 18px 66px 0px rgba(162, 165, 168, 0.20), 0px 2.254px 8.264px 0px rgba(162, 165, 168, 0.41);
	}
	20% {
		transform: scale(1.3);
		z-index: 3;
		box-shadow: 0px 0px 13.519px 0px rgba(34, 243, 175, 0.70), 0px 23.625px 86.625px 0px rgba(162, 165, 168, 0.20), 0px 2.958px 10.847px 0px rgba(162, 165, 168, 0.41);
	}

	70% {
		transform: scale(1.3);
		z-index: 3;
		box-shadow: 0px 0px 13.519px 0px rgba(34, 243, 175, 0.70), 0px 23.625px 86.625px 0px rgba(162, 165, 168, 0.20), 0px 2.958px 10.847px 0px rgba(162, 165, 168, 0.41);
	}
	90% {
		transform: scale(1);
		z-index: 1;
		box-shadow: 0px 18px 66px 0px rgba(162, 165, 168, 0.20), 0px 2.254px 8.264px 0px rgba(162, 165, 168, 0.41);
	}
	100% {
		transform: scale(1);
		z-index: 1;
		box-shadow: 0px 18px 66px 0px rgba(162, 165, 168, 0.20), 0px 2.254px 8.264px 0px rgba(162, 165, 168, 0.41);
	}
}


@keyframes acroPulseUp {
	0% {
		transform: translateY(0);
	}
	20% {
		transform: translateY(8px);
	}

	70% {
		transform: translateY(8px);
	}
	90% {
		transform: translateY(0);
	}
	100% {
		transform: translateY(0);
	}
}

