	70% {
		transform: translate(30px, -30px) scale(1);
		z-index: 2;
		opacity: 1;
		filter: blur(0);
	}
	90% {
		transform: translate(0, 0) scale(0.7);
		z-index: 1;
		opacity: 0.3;
		filter: blur(0.75px);
	}
	100% {
		transform: translate(0, 0) scale(0.7);
		z-index: 1;
		opacity: 0.3;
		filter: blur(0.75px);
	}
}


@keyframes acroStat3Img1 {
	0% {
