		transform: scale(1);
		opacity: 1;
		background: #7048C7;
	}
}
@keyframes acroStat3Img5 {
	0% {
		filter: blur(1.2px);
		width: 150px;
		z-index: 1;
	}
	20% {
		filter: blur(0px);
